@import url(https://fonts.googleapis.com/css?family=Bungee+Inline|Righteous|);

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-family: inherit;
	font-size: 100%;
    box-sizing: border-box;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a {
    text-transform: none;
}
li {
	list-style: none;
}
body {
    width: 100%;
    height: auto;
    background: #141E30;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left top, right top, from(#243B55), to(#141E30));
    background: linear-gradient(to right, #243B55, #141E30); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

form {
    margin: 1rem 0;
}

label span {
    font-family: 'Righteous', sans-serif;
    color: #fff;
    margin-right: 0.5rem;
    font-size: 1.1rem;
}

input {
    width: 10rem;
    font-size: 1.1rem;
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.5);
}

input:focus {
    background-color: rgb(255, 255, 255);
}

.container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

h1 {
    font-size: 2.1rem;
    font-weight: 400;
    padding: 2rem;
    font-family: 'Bungee Inline';
    color: rgb(252, 224, 66);
}

.wrapper {
    width: 100%;
    display: grid;
    grid-template-columns: repeat( auto-fit, minmax(250px, 1fr) );
    grid-gap: 3rem 1rem;
    padding: 2rem;
}

@media screen and (min-width: 600px) {
    h1 {
        font-size: 3rem;
    }

    label span {
        margin-right: 1rem;
        font-size: 1.3rem;
    }

    input {
        width: 15rem;
        font-size: 1.3rem;
    }
}

@media screen and (min-width: 900px) {
    h1 {
        font-size: 6rem;
    }

    label span {
        font-family: 'Righteous', sans-serif;
        color: #fff;
        margin-right: 1rem;
        font-size: 1.5rem;
}

    input {
        width: 15rem;
        font-size: 1.5rem;
    }
}
.container-loading {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loading {
    font-size: 2rem;
    font-family: 'Bungee Inline';
    color: rgb(252, 224, 66);
    -webkit-animation: blink-1 2s infinite both;
            animation: blink-1 2s infinite both;
}

