/**
 * ----------------------------------------
 * animation blink-1
 * ----------------------------------------
 */
@-webkit-keyframes blink-1 {
  0%,
  50%,
  100% {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0;
  }
}
@keyframes blink-1 {
  0%,
  50%,
  100% {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0;
  }
}

@media screen and (min-width: 600px) {
  .loading {
    font-size: 5rem;
  }
}

@media screen and (min-width: 900px) {
  .loading {
    font-size: 6rem;
  }
}
.card {
    width: 250px;
    height: 450px;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    border: 3px solid #141E30;
    display: grid;
    grid-template-rows: 1fr 10fr 1fr;
    grid-gap: 1rem 0;
    overflow: hidden;
    margin: 0 auto;
    padding: 1rem;
    font-family: 'Righteous', sans-serif;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition-duration: .3s;
            transition-duration: .3s;
}

.card:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    cursor: pointer;
}

.title {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    text-align: center;
    text-transform: uppercase;
}

.picture {
    background-image: url("https://yts.mx/assets/images/movies/american_pie_presents_band_camp_2005/medium-cover.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 10px;
}

.box {
    display: flex;
    align-content: center;
    justify-content: space-between;
}

.rating {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1rem;
}

.rating span,
.year span {
    font-weight: 600;
    margin-left: 0.5rem;
}

.year {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1rem;
}
.container-error {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
}

.error {
    font-size: 2rem;
    font-family: 'Bungee Inline';
    border: 5px solid rgb(255, 64, 64);
    color: rgb(255, 64, 64);
    padding: 2rem;
    -webkit-animation: blink-1 5s infinite both;
            animation: blink-1 5s infinite both;
    text-align: center;
}

@media screen and (min-width: 600px) {
    .container-error {
        padding: 5rem;
    }
    .error {
        font-size: 2rem;
    }
}

@media screen and (min-width: 900px) {
    .error {
        font-size: 6rem;
    }
}
